<template>
  <div>
    <b-navbar toggleable="md" type="dark" variant="dark">
      <b-navbar-brand href="#">Conala</b-navbar-brand>
      <b-collapse is-nav id="nav_collapse">
        <b-navbar-nav class="ml-auto">
          <b-nav-item-dropdown right>
            <!-- Using button-content slot -->
            <template slot="button-content">{{ name }}</template>
            <b-dropdown-item v-b-modal.modalChangePass>Change password</b-dropdown-item>
            <b-dropdown-item @click="signout()">Sign out</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-collapse>
    </b-navbar>
    <b-modal id="modalChangePass"
             ref="modalChangePass"
             title="Change password"
             centered
             no-fade
             @ok="changePassword"
             @hide="clearPasswords">
      <b-alert :show="changePasswordErrorMessage !== null" dismissible variant="danger"
        >{{ changePasswordErrorMessage }}</b-alert>
      <form v-if="showModal">
        <b-form-group label="Current Password:"
                      label-for="currentPassword">
          <b-form-input id="currentPassword"
                        ref="currentPassword"
                        type="password" v-model="currentPassword" required
                        placeholder="Enter current password"
          ></b-form-input>
        </b-form-group>
        <b-form-group label="New Password:"
                      label-for="newPassword">
          <b-form-input id="newPassword"
                        ref="newPassword"
                        type="password" v-model="newPassword" required
                        placeholder="Enter new password"
          ></b-form-input>
        </b-form-group>
      </form>
    </b-modal>
    <b-container v-if='authed'>
      <b-alert :show="dismissCountDown"
               dismissible
               variant="success"
               @dismissed="dismissCountDown=0"
               @dismiss-count-down="countDownChanged"
          >{{ successMessage }}</b-alert>
      <b-card no-body>
        <b-tabs card no-fade v-model="tabIndex">
          <b-tab title="Progress">
            <b-pagination align="center" :total-rows="totalAnnotations" :per-page="historyPageLength" v-model="historyPageIndex" />
            <b-table bordered hover striped :items="historyItems" :fields="historyFields">
              <template slot="actions" slot-scope="row">
                <b-btn size="sm" @click.stop="check(row.item.id)">Check</b-btn>
              </template>
            </b-table>
          </b-tab>
          <b-tab title="Workspace">
            <b-card-group deck class="mb-3">
              <b-card border-variant="primary"
                      header-bg-variant="primary"
                      header-text-variant="white">
                <div slot="header">
                  <b-row align-h="between">
                    <b-col cols="auto">
                      <b-btn variant="success" @click="save('save')" :pressed="candidate.decision==='Saved'">Save</b-btn>
                      <b-btn variant="danger" @click="save('discard')" :pressed="candidate.decision==='Discarded'">Discard</b-btn>
                      <b-btn variant="warning" @click="save('skip')" :pressed="candidate.decision==='Skipped'">Skip</b-btn>
                      <b-btn variant="outline-light" :disabled="candidateId <= 0" @click="previous()">Previous</b-btn>
                      <b-btn variant="outline-light" :disabled="candidateId >= totalAnnotations" @click="next()">Next</b-btn>
                      <b-btn variant="outline-light" @click="latest()">Latest</b-btn>
                    </b-col>
                    <b-col cols="auto">{{ candidate.decision }} Candidate - [{{ candidateId }}]</b-col>
                  </b-row>
                </div>
                <div>
                  <b-form-textarea v-model="candidate.intent"/>
                </div>
                <div>
                  <pre class="mt-3" v-highlightjs="candidate.snippet"><code></code></pre>
                </div>
              </b-card>
            </b-card-group>
            <b-card-group deck class="mb-3">
              <b-card border-variant="success"
                      header-bg-variant="success"
                      header-text-variant="white">
                <div slot="header">
                  <b-row align-h="between">
                    <b-col cols="auto">{{ body.title }}</b-col>
                    <b-col cols="auto">Answer</b-col>
                  </b-row>
                </div>
                <div v-highlightjs v-html="body.answerBody"/>
              </b-card>
            </b-card-group>
            <b-card-group deck class="mb-3">
              <b-card border-variant="info"
                      header-bg-variant="info"
                      header-text-variant="white">
                <div slot="header">
                  <b-row align-h="between">
                    <b-col cols="auto">{{ body.title }}</b-col>
                    <b-col cols="auto">Question</b-col>
                  </b-row>
                </div>
                <div v-highlightjs v-html="body.postBody"/>
              </b-card>
            </b-card-group>
          </b-tab>
          <b-tab title="Invitation" v-if="admin">
            <b-row class="mb-2">
              <b-col md>
                <b-btn variant="primary" @click="generateInviteCode">Generate a new code</b-btn>
              </b-col>
            </b-row>
            <b-pagination align="center" :total-rows="totalInviteCodes" :per-page="invitePageLength" v-model="invitePageIndex" />
            <b-table bordered hover striped :items="inviteItems" :fields="inviteFields">
              <template slot="actions" slot-scope="row">
                <b-btn size="sm" v-clipboard:copy="row.item.code" v-clipboard:success="copyed">Copy</b-btn>
                <b-btn size="sm" variant="danger" @click.stop="deleteCode(row.item.code)">Delete</b-btn>
              </template>
            </b-table>
          </b-tab>
        </b-tabs>
      </b-card>
    </b-container>
  </div>
</template>

<script>
import axios from 'axios'
import router from '@/router'
import store from '@/store'

export default {
  data: function () {
    return {
      dismissCountDown: 0,
      successMessage: null,
      authed: store.state.isLogged,
      admin: store.state.isAdmin,
      name: localStorage.getItem('name'),
      currentPassword: null,
      newPassword: null,
      changePasswordErrorMessage: null,
      showModal: true,
      tabIndex: 0,
      historyPageIndex: 1,
      historyPageLength: 10,
      totalAnnotations: 0,
      historyFields: ['id', 'title', 'decision', 'actions'],
      historyItems: [],
      invitePageIndex: 1,
      invitePageLength: 10,
      totalInviteCodes: 0,
      inviteFields: ['code', 'actions'],
      inviteItems: [],
      candidateId: null,
      candidate: {
        intent: null,
        snippet: null,
        decision: null
      },
      body: {
        title: null,
        postBody: null,
        answerBody: null
      }
    }
  },
  beforeCreate () {
    if (!store.state.isLogged) {
      router.push('/login')
    } else {
      this.authed = true
    }
  },
  methods: {
    generateInviteCode () {
      this.successMessage = null
      let self = this
      const path = '/api/invite_code'
      axios.post(path, {}).then(response => {
        self.successMessage = response.data.msg
        self.loadInviteCode()
      })
    },
    deleteCode (code) {
      this.successMessage = null
      let self = this
      const path = `/api/invite_code/${code}`
      axios.delete(path).then(response => {
        self.successMessage = response.data.msg
        self.loadInviteCode()
      })
    },
    copyed (evt) {
      this.successMessage = `Code "${evt.text}" has been copied to clipboard`
    },
    countDownChanged (val) {
      this.dismissCountDown = val
    },
    loadHistory () {
      let self = this
      let startIndex = (this.historyPageIndex - 1) * this.historyPageLength
      const path = `/api/history/${startIndex}/${this.historyPageLength}`
      axios.get(path)
        .then(response => {
          self.totalAnnotations = response.data.count
          self.historyItems = response.data.items
          if (self.candidateId === null) {
            self.candidateId = response.data.count
          }
        })
        .catch(error => {
          console.log(error)
        })
    },
    loadInviteCode () {
      let self = this
      let startIndex = (this.invitePageIndex - 1) * this.invitePageLength
      const path = `/api/invite_code/${startIndex}/${this.invitePageLength}`
      axios.get(path)
        .then(response => {
          self.totalInviteCodes = response.data.count
          self.inviteItems = response.data.items
        })
        .catch(error => {
          console.log(error)
        })
    },
    loadCandidate (candidateId) {
      let self = this
      const path = `/api/candidate/${candidateId}`
      axios.get(path)
        .then(response => {
          self.candidate = response.data
          const postId = response.data.postId
          const answerId = response.data.answerId
          const path = `/api/body/${postId}/${answerId}`
          axios.get(path)
            .then(response => {
              self.body = response.data
            })
            .catch(error => {
              console.log(error)
            })
        })
        .catch(error => {
          console.log(error)
        })
    },
    save (action) {
      let self = this
      const path = `/api/save/${this.candidateId}`
      axios.post(path, {
        action: action,
        rewerittenIntent: self.candidate.intent
      }).then(response => {
        let pageStart = (self.historyPageIndex - 1) * self.historyPageLength
        let pageEnd = self.historyPageIndex * self.historyPageLength
        if (self.candidateId >= pageStart && self.candidateId < pageEnd) {
          self.loadHistory()
        }
        self.totalAnnotations = response.data.count
        self.candidateId++
      })
        .catch(error => {
          console.log(error)
        })
    },
    previous () {
      if (this.candidateId > 0) {
        this.candidateId--
      }
    },
    next () {
      if (this.candidateId < this.totalAnnotations) {
        this.candidateId++
      }
    },
    check (candidateId) {
      this.candidateId = candidateId
      this.tabIndex = 1
    },
    latest () {
      this.candidateId = this.totalAnnotations
    },
    signout () {
      store.commit('LOGOUT_USER')
      router.push('/login')
    },
    changePassword (evt) {
      this.successMessage = null
      this.changePasswordErrorMessage = null
      let self = this
      evt.preventDefault()
      const path = '/api/change_password'
      axios.post(path, {
        currentPassword: this.currentPassword,
        newPassword: this.newPassword
      }).then(response => {
        self.$refs.modalChangePass.hide()
        self.successMessage = response.data.msg
      })
        .catch(error => {
          if (error.response) {
            let response = error.response
            self.changePasswordErrorMessage = response.data.msg
          }
          if (!self.changePasswordErrorMessage) {
            self.changePasswordErrorMessage = error.message
          }
          console.log(error)
        })
    },
    clearPasswords () {
      this.currentPassword = null
      this.newPassword = null
      this.changePasswordErrorMessage = null
      this.showModal = false
      let self = this
      this.$nextTick(() => {
        self.showModal = true
      })
    }
  },
  watch: {
    successMessage: function (val) {
      if (val !== null) {
        this.successMessage = val
        this.dismissCountDown = 10
      }
    },
    candidateId: function (val) {
      this.loadCandidate(val)
      this.candidateId = val
    },
    historyPageIndex: function (val) {
      this.historyPageIndex = val
      this.loadHistory()
    },
    invitePageIndex: function (val) {
      this.invitePageIndex = val
      this.loadInviteCode()
    }
  },
  mounted: function () {
    this.loadHistory()
    if (this.admin) {
      this.loadInviteCode()
    }
  }
}
</script>



